  usernameMaxLength: 20,
  passwordMinLength: 8,
  passwordMaxLength: 30,
  roleNameMaxLength: 20,
  permissionNameMaxLength: 20,
  isEmail: (value) => !value || /.+@.+\..+/.test(value) || "邮箱格式有误",
  isInteger: (value) => !value || /^\d+$/.test(value) || "只能输入整数",
  isFloat: (value) => !value || /^(-)?[1-9]+[0-9]*(.[0-9]{1,2})?$/.test(value) || "请输入正确格式的数字",
  isPositive: (value) => !value || /^[1-9]+[0-9]*(.[0-9]{1,2})?$/.test(value) || "请输入正确格式的正数",
  isNegative: (value) => !value || /^-[1-9]+[0-9]*(.[0-9]{1,2})?$/.test(value) || "请输入正确格式的负数",
  isUsername: (value) => {
    if (!!value && value.length <= 20) {
      return true;
    }
    if (!value) {
      return '请输入用户名';
    }
    if (value.length > 20) {
      return '用户名不能超过20位';
    }
  },
  isPassword: (value) => {
    if (!!value && value.length >= 8 && value.length <= 30) {
      return true;
    }
    if (!value) {
      return '请输入密码';
    }
    if (value.length < 8) {
      return '密码不能少于8位';
    }
    if (value.length > 30) {
      return '密码不能超过30位';
    }
  },
  isphoneNumber: (value) => {
    if (!value) {
      return true;
    }
    if (/^1\d+$/.test(value) && value.length === 11) {
      return true;
    }
    if (!/^1\d+$/.test(value)) {
      return '手机号格式不正确'
    }
    if (!(value.length === 11)) {
      return '手机号只能为11位';
    }
  },
  isRoleName: (value) => {
    if (!!value && value.length <= 20) {
      return true;
    }
    if (!value) {
      return '请输入角色名称';
    }
    if (value.length > 20) {
      return '角色名称不能超过20位';
    }
  },
  isPermissionName: (value) => {
